		created() {
			document.documentElement.scrollTop = 0;
		},
		mounted() {
			let that = this;
			let csid = that.$route.params.id; // 当前id
			that.csid = csid;
			console.log(csid);
			window.addEventListener("scroll", this.handleScroll);
			setTimeout(() => (that.loading = false), 300);
		},
		computed: mapState(["langType", "isLogin", "isfixed"]),
		methods: {
			//页面滚动
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //导航背景-灰色
				} else {
					that.ishowbg = false; //导航背景-白色
				}
			},

			//复制链接
			copyFont(cptxt) {
				var that = this;
				that.$mpublic.copyFont(cptxt).then(() => {
					that.$message.success("已复制");
				});
			},

			//切换选项卡
			handleClick(idx) {
				var that = this;
				that.ctabid = idx;
			},

			//显示弹窗-绑定钱包
			showAlert() {
				let that = this;
				that.$refs.mwallet.showAlert(true);
			},

		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //销毁监听
		}
	}
