import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'

Vue.use(Vuex) 
// 新窗口打开页面时，保证数据不丢失
const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
  reducer: state => ({
    langType: state.langType, // 这个就是存入localStorage的值
    isLogin: state.isLogin,  
    isfixed: state.isfixed,  
    mcastlist: state.mcastlist,  
  })
})
const store = new Vuex.Store({
  state: {
    langType: "ZH_CN",
	isLogin: false, //是否登录
	isfixed: false, //是否禁止页面滚动
	mcastlist:[], //已铸造列表
  },
  mutations: {
	//改变语言 
    changeLanguageOld (state, clang) {
      state.langType = clang
    },
	
	//改变登录状态
	changeLoginOld (state, isact) {
	  state.isLogin = isact
	},
	
	//改变页面滚动状态
	changePageOld (state, isact) {
	  state.isfixed = isact
	},
	
	//添加已铸造列表
	changeCastListOld (state, mlist) {
	  state.mcastlist = mlist
	},
	
  },
  actions: {},
  plugins: [vuexLocal.plugin]
})

export default store


