//铸造（用户上传图片到链上）
async function setCastImg(userAddr, imgUrl, callback) {
	var encode_api_data = castImg.methods.mint(userAddr, imgUrl).encodeABI();
	const parameters = {
		to: ImgAddress, //合约地址
		from: ethereum.selectedAddress,
		data: encode_api_data
	};
	await ethereum.sendAsync({
		method: 'eth_sendTransaction',
		params: [parameters],
	}, function(err, response) {
		if (err) {
			callback({
				code: -4001,
				message: 'cancel'
			});
			return;
		}
		const rejected = 'MetaMask Tx Signature: User denied transaction signature'
		if (response.error && response.error.message.includes(rejected)) {
			callback({
				code: -4001,
				message: "cancel"
			});
			return;
		}
		if (response.code == "-32603") {
			callback({
				code: -32603,
				message: "fail"
			});
			return;
		}
		if (response.error && response.error.code == "-32603") {
			callback({
				code: -32603,
				message: "fail"
			});
			return;
		}
		if (response.result) {
			callback('4444');
			queryTransaction(response.result,callback);
		}
	})
}


// 查询交易是否完成，这里要通过这个方法去一直查询交易是否完成
function queryTransaction(tx,callback){
	let number_takeGain = 0;
	let timer1 = setTimeout(function(){
		if (number_takeGain > 30) {
			clearTimeout(timer1);
			number_takeGain = 1;
			callback('5555');
			return false;
		} else {
			// 查询交易是否完成，这里要通过这个方法去一直查询交易是否完成
			web3.eth.getTransactionReceipt(tx).then(function(res) {
				// console.log(res);
				if (res && res.status) {
					clearTimeout(timer1);
					callback(res);
					return;
				} else {
					number_takeGain++;
					queryTransaction(tx,callback);
				}
			});
		}
	},2000);
}


//商品上架
async function shelvesImg(sid, sprice, startime, endtime, callback) {
	var encode_api_data = castImg.methods.listing(sid, sprice, startime, endtime).encodeABI();
	const parameters = {
		to: ImgAddress, //合约地址
		from: ethereum.selectedAddress,
		data: encode_api_data
	};
	await ethereum.sendAsync({
		method: 'eth_sendTransaction',
		params: [parameters],
	}, function(err, response) {
		if (err) {
			callback({
				code: -4001,
				message: 'cancel'
			});
			return;
		}
		const rejected = 'MetaMask Tx Signature: User denied transaction signature'
		if (response.error && response.error.message.includes(rejected)) {
			callback({
				code: -4001,
				message: "cancel"
			});
			return;
		}
		if (response.code == "-32603") {
			callback({
				code: -32603,
				message: "fail"
			});
			return;
		}
		if (response.error && response.error.code == "-32603") {
			callback({
				code: -32603,
				message: "fail"
			});
			return;
		}
		if (response.result) {
			callback('4444');
			let number_takeGain = 0;
			timer_takeGain = setInterval(() => {
				if (number_takeGain > 30) {
					window.clearInterval(timer_takeGain);
					callback({
						code: -9999,
						message: "超时"
					})
					number_takeGain = 1;
				} else {
					// 查询交易是否完成，这里要通过这个方法去一直查询交易是否完成
					web3.eth.getTransactionReceipt(response.result).then(function(res) {
						console.log(res);
						if (res && res.status) {
							window.clearInterval(timer_takeGain);
							callback(res);
						} else {
							number_takeGain++
						}
					});
				}
			}, 2000);
		}
	})
}


