//领取空投
async function getAirdrop(recomm, callback) {
	var encode_api_data = metacoin.methods.airdrop(recomm).encodeABI();
	const parameters = {
		to: airdropAddress, //合约地址
		from: ethereum.selectedAddress,
		data: encode_api_data
	};
	await ethereum.sendAsync({
		method: 'eth_sendTransaction',
		params: [parameters],
	}, function(err, response) {
		if (err) {
			callback({
				code: -4001,
				message: 'cancel'
			});
			return;
		}
		const rejected = 'MetaMask Tx Signature: User denied transaction signature'
		if (response.error && response.error.message.includes(rejected)) {
			callback({
				code: -4001,
				message: "cancel"
			});
			return;
		}
		if (response.code == "-32603") {
			callback({
				code: -32603,
				message: "fail"
			});
			return;
		}
		if (response.error && response.error.code == "-32603") {
			callback({
				code: -32603,
				message: "fail"
			});
			return;
		}
		// if(response.result) {
		//   callback({code:200,data:response.result,status:true})
		// }

		// if (response.result) {
		//   //监听合约
		//   metacoin.events.Airdrop({},{frommBlock:0,toBlock:'latest'},function(error,eve){
		//     if(eve)
		//       callback({code:200,data:eve,status:true})
		//     else
		//       callback(error)
		//   })
		// }

		if (response.result) {
			let number_takeGain = 0;
			timer_takeGain = setInterval(() => {
				number_takeGain++
				// 查询交易是否完成，这里要通过这个方法去一直查询交易是否完成
				web3.eth.getTransactionReceipt(response.result).then(function(res) {
					if (res == null) {
						callback(res)
					} else if (res.status) {
						callback(res)
						clearInterval(timer_takeGain);
					} else {
						clearInterval(timer_takeGain);
					}
				});
				if (number_takeGain > 30) {
					clearInterval(timer_takeGain);
					callback({
						code: -9999,
						message: "超时"
					})
					number_takeGain = 1;
				}
			}, 1000);
		}
	})
}


