<template>
	<div id="user">
		<mHeader :clink='clink' :ishow="ishowbg"></mHeader>
		<el-image :src="userInfo.user_bg" fit="cover" class="uImgbg"></el-image>
		<div class="aboutbox2">
			<div class="mbflex dl trans3" v-for="(item,index) in userInfo.Website" :key="index">
				<i class="iconfont dic1 trans3" :class="item.icon"></i>
				<p class="p1 p-hide">{{item.name}}</p>
			</div>
		</div>
		<div class="raboutbox">
			<div class="aboutbox1">
				<el-image :src="userInfo.user_head" fit="cover" class="img1"></el-image>
				<h2 class="p1 p-hide">{{userInfo.user_name}}</h2>
				<p class="p4">越努力越幸运</p>
				<div class="mbflex p2 acurs" v-if="userBagAddress" @click="copyFont(userBagAddress)">
					<i class="iconfont icon-ethereum dic1"></i>
					<p class="sp2 p-hide">{{userBagAddress}}</p>
					<i class="iconfont icon-fuzhi dic2"></i>
				</div>
				<div class="mbflex p2 bgray">
					<p class="sp2 p-hide">编辑资料</p>
					<i class="iconfont icon-shouhuodizhiyebianji dic2"></i>
				</div>
				<div class="mbflex p3">
					<p class="sp1 p-hide">拥有：456</p>
					<p class="sp1 p-hide">铸造：162</p>
				</div>
			</div>

			<div class="allbox-hd">
				<div class="aboutbox3 rmubox3">
					<div class="mbflex tabox1">
						<div class="dl" :class="{'act':ctabid==index}" v-for="(item,index) in tablist" :key="index"
							@click="handleClick(index)">
							{{item}}
						</div>
					</div>

					<div class="contbox2">
						<div class="goods-xqbox3" v-show="ctabid == 0">
							<div class="box3" v-if="mcastlist.length > 0">
								<el-row :gutter="35">
									<el-col v-for="(item,index) in mcastlist" :sm="24" :md="8" :key="index">
										<div class="dl" v-if="userBagAddress == item.uaddr">
											<router-link :to="'/goodsDetailCast/' + item.id">
												<div class="dimg">
													<el-image :src="item.img" fit="cover" class="img1 trans5">
													</el-image>
												</div>
												<div class="pbox">
													<div class="mbflex p2">
														<label class="cor1 p-hide">{{item.title}}</label>
														<div class="mbflex mdic" v-if="item.price > 0">
															<p class="spjg">{{item.price}} BNB</p>
														</div>
													</div>
												</div>
											</router-link>
										</div>
									</el-col>
								</el-row>
							</div>
							<el-empty v-else description="暂无数据"></el-empty>
						</div>

						<div class="goods-xqbox3" v-show="ctabid == 1">
							<div class="box3" v-if="mcastlist.length > 0">
								<el-row :gutter="35">
									<el-col v-for="(item,index) in mcastlist" :sm="24" :md="8" :key="index">
										<div class="dl" v-if="userBagAddress == item.uaddrCast">
											<router-link :to="'/goodsDetailCast/' + item.id">
												<div class="dimg">
													<el-image :src="item.img" fit="cover" class="img1 trans5">
													</el-image>
												</div>
												<div class="pbox">
													<div class="mbflex p2">
														<label class="cor1 p-hide">{{item.title}}</label>
														<div class="mbflex mdic" v-if="item.price > 0">
															<p class="spjg">{{item.price}} BNB</p>
														</div>
													</div>
												</div>
											</router-link>
										</div>
									</el-col>
								</el-row>
							</div>
							<el-empty v-else description="暂无数据"></el-empty>
						</div>

						<div class="rmhislist" v-show="ctabid == 2">
							<div v-if="rmhislist.length > 0">
								<div class="mbflex dl trans5" v-for="(item,index) in rmhislist" :key="index">
									<el-image :src="item.img" fit="cover" class="img1 trans5"></el-image>
									<div class="pbox">
										<div class="p1 p-hide">
											<label class="sp1">{{item.title}}</label>
											<label class="sp2">{{item.uname}}</label>
										</div>
										<p class="p2">{{item.time}}</p>
									</div>
									<label v-if="item.price1" class="cspr">{{item.price1}} BNB</label>
								</div>
							</div>
							<el-empty v-else description="TA还没有关注"></el-empty>
						</div>
					</div>
				</div>
			</div>
		</div>

		<!-- 底部 -->
		<mFooter :isform="isform"></mFooter>
		<!-- 底部 -->

		<!-- 绑定钱包 -->
		<Wallet ref="mwallet" @setWaddr="setWaddr"></Wallet>
		<!-- 绑定钱包 -->
	</div>
</template>

<script>
	import International from '../lib/international.js' //语言包
	import mHeader from '@/components/mheader' //顶部
	import mFooter from '@/components/mfooter' //底部
	import Wallet from '@/components/wallet' //绑定钱包
	import {getAccount} from '@/util/myconnect'

	import {
		mapMutations,
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				International,
				clink: 4, //当前页
				isform: true, //不显示底部表单
				ishowbg: false, //导航背景
				loading: false, //开启骨架屏
				iUser: false, //是否用户本人
				uwallet: "", //用户钱包地址
				userInfo: {
					user_name: "peajey",
					user_head: require('../../static/img/ban_03.jpg'),
					user_bg: "https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimg9.51tietu.net%2Fpic%2F2019-091123%2Fzfche3r1q0hzfche3r1q0h.jpg&refer=http%3A%2F%2Fimg9.51tietu.net&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1653114114&t=f016530e92fce8945d2056eef61f2002",
					user_addr: "0xE351...cEbB3b",
					Website: [{
						icon: "icon-weibo-copy",
						url: "",
						name: "https://linktr.ee/peajey",
					}, {
						icon: "icon-youxiang",
						url: "",
						name: "23685559@qq.com",
					}, {
						icon: "icon-QQ-circle-fill",
						url: "",
						name: "23685559",
					}, {
						icon: "icon-weixin",
						url: "",
						name: "weixow12",
					}],
				}, //个人资料
				ctabid: 0, //当前分类
				tablist: ['拥有', '铸造', '活动'],
				rmhislist: [{
					img: require('../../static/img/pic_05.jpg'),
					title: "铸造者",
					uname: "子非鱼",
					time: "2022 年 5 月 16 日 10:16",
					price1: 1.20,
				}],
				userBagAddress: "", //用户钱包地址
			}
		},

		components: {
			mHeader,
			mFooter,
			Wallet
		},
		created() {
			let that = this;
			that.myAccount();
			document.documentElement.scrollTop = 0;
		},
		mounted() {
			let that = this;
			window.addEventListener("scroll", this.handleScroll);
			setTimeout(() => (that.loading = false), 300);
		},
		computed: mapState(["langType", "isLogin", "isfixed", "mcastlist"]),
		methods: {
			...mapMutations(["changeLoginOld", "changePageOld", "changeCastListOld"]),

			//页面滚动
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //导航背景-灰色
				} else {
					that.ishowbg = false; //导航背景-白色
				}
			},

			//退出登录
			setExit() {
				let that = this;
				that.$confirm('退出登录后将不能继续使用个人中心的功能', '确定退出?', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					that.changeLoginOld(false); //改变登录状态
					that.$router.push({
						path: '/'
					}); //跳回首页
				}).catch(() => {});
			},

			//显示弹窗-绑定钱包
			showAlert() {
				let that = this;
				that.$refs.mwallet.showAlert(true);
			},

			//获取创建的钱包地址
			setWaddr(waddr) {
				let that = this;
				that.uwallet = waddr;
			},

			//切换选项卡
			handleClick(idx) {
				this.ctabid = idx;
			},

			myAccount() {
				//获取个人信息
				let that = this;
				getAccount().then((res) => {
					if (res.length > 0) {
						that.userBagAddress = res[0];
					}
				});
			},
			
			//复制链接
			copyFont(cptxt) {
				var that = this;
				that.$mpublic.copyFont(cptxt).then(() => {
					that.$message.success("已复制");
				});
			},

		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //销毁监听
		}
	}
</script>

<style>
	.mshezhi {
		font-size: 2.2rem;
		position: absolute;
		right: 4%;
		top: 1rem;
		z-index: 1001;
		color: #000;
	}

	.goods-xqbox3 .box3 .p2 .cor1 {
		font-size: 1.3rem;
		font-weight: bold;
	}

	.goods-xqbox3 .box3 .p2 .spjg {
		color: #000;
		font-size: 1.4rem;
	}
</style>
