<template>
	<div id="rupublish">
		<mHeader :clink='clink' :ishow="ishowbg"></mHeader>
		<!-- 个人中心-新建作品 -->
		<div class="allbox-hd">
			<el-form ref="form">
				<div class="upublish">
					<el-upload class="dimg" :data="imgData" action="http://cqcd.duckdns.org:5000/api/v1/upload"
						:show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload"
						:on-error="handleAvatarError" drag>
						<el-image v-if="imageUrl" :src="imageUrl" fit="cover" class="img1"></el-image>
						<el-image v-else :src="upbg" fit="cover" class="img1"></el-image>
					</el-upload>
					<div class="mbflex put">
						<p class="p1">标题</p>
						<el-input v-model="title" placeholder="请输入标题" class="dwr ipu1"></el-input>
					</div>
					<div class="mbflex put">
						<p class="p1">简介</p>
						<el-input type="textarea" v-model="intro" placeholder="请输入简介" class="dwr ipu1 mare1"></el-input>
					</div>
					<button type="button" class="btntj trans5" @click="onSubmit(imageUrl,title,intro)">铸造</button>
				</div>
			</el-form>
		</div>

		<!-- 底部 -->
		<mFooter></mFooter>
		<!-- 底部 -->
		
		<!-- 进度条 -->
		<div class="mtipbox trans5" :class="{'act': ispros}">
			<div class="mbflex box1">
				<div class="dl">
					<div class="bg1"></div>
					<p class="p1">连接钱包</p>
				</div>
				<div class="dl">
					<div class="bg1" :class="{'act': cpros==1}">
						<div class="bg2"></div>
					</div>
					<p class="p1">文件上传</p>
				</div>
				<div class="dl">
					<div class="bg1" :class="{'act': cpros==2}">
						<div class="bg2"></div>
					</div>
					<p class="p1">铸造</p>
				</div>
			</div>
		</div>
		<!-- 进度条 -->

	</div>
</template>

<script>
	import International from '../lib/international.js' //语言包
	import mHeader from '@/components/mheader' //顶部
	import mFooter from '@/components/mfooter' //底部
	import {
		setCastImg,
		getAccount
	} from '@/util/myconnect'

	import {
		mapMutations,
		mapState
	} from 'vuex'
	var Web3 = require("web3");
	export default {
		name: 'rupublish',
		data() {
			return {
				International,
				userBagAddress: "", //用户钱包地址
				clink: 5, //当前页
				ishowbg: false, //导航背景
				title: "", //标题
				intro: "", //简介
				imageUrl: "", //作品图片
				upbg: require('../../static/img/upbg.jpg'),
				imgData: {
					image:"",
					filename:"",
				}, //上传图片参数
				ispros: false, //是否显示进度条
				cpros: 1, //当前进度
			}
		},

		components: {
			mHeader,
			mFooter
		},
		created() {
			let that = this;
			document.documentElement.scrollTop = 0;
			that.myAccount();
		},
		mounted() {
			let that = this;
			// let ar1=[];
			// that.changeCastListOld(ar1); //清空铸造列表
			window.addEventListener("scroll", this.handleScroll);
		},
		computed: mapState(["langType", "isLogin", "isfixed", "mcastlist"]),
		methods: {
			...mapMutations(["changeLoginOld", "changePageOld", "changeCastListOld"]),

			//页面滚动
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //导航背景-灰色
				} else {
					that.ishowbg = false; //导航背景-白色
				}
			},
			
			//文件上传成功
			handleAvatarSuccess(res, file) {
				let that = this;
				console.log(res);
				that.imageUrl = res;
			},
			
			//文件上传失败
			handleAvatarError(err, file, fileList){
				let that = this;
				this.$message.error("文件上传失败，请重新上传！");
			},
			
			//文件上传之前
			beforeAvatarUpload(file) {
				let that = this;
				let imgData = that.imgData;
				const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isJPG) {
					this.$message.error('上传图片只能是png、jpg或jpeg格式!');
				}
				if (!isLt2M) {
					this.$message.error('上传图片大小不能超过 2MB!');
				}
				imgData.image = file;
				imgData.filename = file.name;
				that.imgData = imgData;
			},

			//切换选项卡
			handleClick(idx) {
				this.ctabid = idx;
			},

			//提交
			onSubmit(imageUrl, title, intro) {
				let that = this;
				var reg = /\d+(\.\d{0,2})?/; //数字验证
				let userBagAddress = that.userBagAddress; //用户钱包地址
				let mcastlistOld = that.mcastlist; //铸造列表
				that.$mpublic.isRequired(imageUrl).then(() => {
					that.$mpublic.isRequired(title).then(() => {
						that.ispros = true;	
						setCastImg(userBagAddress, imageUrl, function(res) {
							console.log(res);	
							if(res == '4444'){
								that.cpros = 2;
							}
							if (res && res.status) {
								that.$message.closeAll();
								let toenkId = res.logs[0].topics[3];
								let imgId = Web3.utils.hexToNumber(toenkId);
								mcastlistOld.unshift({
									id: imgId,
									img: imageUrl, //图片地址
									title: title, //标题
									intro: intro, //简介
									price: 0, //价格
									stime: "", //开始时间
									etime: "", //截止时间
									type: 0, //'0=拥有', '1=盲盒', '2=竞拍'
									isup: false, //是否上架
									isell: false, //是否被购买
									uaddr: userBagAddress, //拥有者钱包地址
									uaddrCast: userBagAddress, //创建者钱包地址
								});
								that.changeCastListOld(mcastlistOld); //更新铸造列表
								that.$message.success("铸造成功");
								window.location.reload();
								that.ispros = false;
								that.cpros = 1;
							}
							
							if(res.code == -4001){
								that.ispros = false;
								that.cpros = 1;
							}
						})
					}, err => {
						that.$message.warning("请输入标题");
					})
				}, err => {
					that.$message.warning("请上传图片");
				})
			},

			myAccount() {
				//获取个人信息
				let that = this;
				getAccount().then((res) => {
					if (res.length > 0) {
						that.userBagAddress = res[0];
					}
				});
			},

		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //销毁监听
		}
	}
</script>

<style>

