			onSubmit(price1, price2, mdatetime1) {
				let that = this;
				var reg = /\d+(\.\d{0,2})?/; //数字验证
				let ctabid = that.ctabid;
				let userBagAddress = that.userBagAddress; //用户钱包地址
				let mcastlistOld = that.mcastlist; //铸造列表
				let sid = that.sid;
				let csid = that.csid;
				let sprice = 0;
				if (ctabid == 2) {
					if (!reg.test(price2)) {
						that.$message.warning("请输入正确的价格");
						that.price2 = "";
						return false;
					}
					sprice = price2;
				} else {
					if (!reg.test(price1)) {
						that.$message.warning("请输入正确的价格");
						that.price1 = "";
						return false;
					}
					sprice = price1;
				}
				let mnumber = Web3.utils.toWei(sprice, 'ether');
				that.$mpublic.isRequired(mdatetime1).then(() => {
					that.ispros = true;
					that.ispros1 = false;
					that.ispros2 = false;
					let stime = mdatetime1[0] / 1000;
					let etime = mdatetime1[1] / 1000;
					shelvesImg(sid, mnumber, stime, etime, function(res) {
						console.log(res);
						if (res == '4444') {
							that.cpros = 2;
						}
						if (res && res.status) {
							that.$message.closeAll();
							for (var i = 0; i < mcastlistOld.length; i++) {
								if (sid == mcastlistOld[i].id) {
									mcastlistOld[i].price = sprice;
									mcastlistOld[i].stime = stime;
									mcastlistOld[i].etime = etime;
									mcastlistOld[i].type = ctabid;
									mcastlistOld[i].isup = true;
								}
							}
							that.changeCastListOld(mcastlistOld); //更新铸造列表
							that.$message.success("提交成功");
							window.location.reload();
							that.visible1 = false;
							that.ispros = false;
							that.cpros = 1;
						} 
						
						if(res.code == -4001) {
							that.ispros = false;
							that.cpros = 1;
						}
					})
				}, err => {
					that.$message.warning("请选择起止日期");
				})
			},

			//购买
			btnBuyImg() {
				let that = this;
				let mcastlistOld = that.mcastlist; //铸造列表
				let sid = that.sid;
				let csid = that.csid;
				let sprice = that.sprice;
				let mnumber = Web3.utils.toWei(sprice, 'ether');
				let mnumber1 = Web3.utils.numberToHex(mnumber);
				let userBagAddress = that.userBagAddress; //用户钱包地址
				that.ispros = false;
				that.ispros1 = true;
				that.ispros2 = false;
				buyImg(sid, mnumber1, function(res) {
					console.log(res);
					if (res == '4444') {
						that.cpros1 = 2;
					}
					if (res && res.status) {
						that.$message.closeAll();
						for (var i = 0; i < mcastlistOld.length; i++) {
							if (sid == mcastlistOld[i].id) {
								mcastlistOld[i].price = sprice;
								mcastlistOld[i].isell = true;
								mcastlistOld[i].isup = false; //被购买的作品下架
								mcastlistOld[i].uaddr = userBagAddress;
							}
						}
						that.changeCastListOld(mcastlistOld); //更新铸造列表
						that.$message.success("提交成功");
						window.location.reload();
						that.ispros1 = false;
						that.cpros1 = 1;
					}
					
					if(res.code == -4001) {
						that.ispros1 = false;
						that.cpros1 = 1;
					}
				})
			},

			//转移
			onSubmitTrans(userBagAddress, addrTo, sid) {
				let that = this;
				let mcastlistOld = that.mcastlist; //铸造列表
				let csid = that.csid;
				that.$mpublic.isRequired(userBagAddress).then(() => {
					that.ispros2 = true;
					that.ispros = false;
					that.ispros1 = false;
					transferImg(userBagAddress, addrTo, sid, function(res) {
						console.log(res);
						if (res == '4444') {
							that.cpros2 = 2;
						}
						if (res && res.status) {
							that.$message.closeAll();
							for (var i = 0; i < mcastlistOld.length; i++) {
								if (csid == i) {
									mcastlistOld[i].uaddr = addrTo;
								}
							}
							that.changeCastListOld(mcastlistOld); //更新铸造列表
							that.$message.success("转移成功");
							window.location.reload();
							that.visible2 = false;
							that.ispros2 = false;
							that.cpros2 = 1;
						}
						
						if(res.code == -4001) {
							that.ispros2 = false;
							that.cpros2 = 1;
						}
					})
				}, err => {
					that.$message.warning("请输入对方钱包地址");
				})
			},

			myAccount() {
				//获取个人信息
				let that = this;
				getAccount().then((res) => {
					if (res.length > 0) {
						that.userBagAddress = res[0];
					}
				});
			},

			//查询nft图片链接
			mgetImgLink(sid) {
				let that = this;
				getImgLink(sid).then((res) => {
					if (res.length > 0) {
						that.imgLink = res;
					}
				});
			},

			//复制链接
			copyFont(cptxt) {
				var that = this;
				that.$mpublic.copyFont(cptxt).then(() => {
					that.$message.success("已复制");
				});
			},

		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //销毁监听
		}
	}
</script>
