<script>
	import International from '../lib/international.js' //语言包
	import mHeader from '@/components/mheader' //顶部
	import mFooter from '@/components/mfooter' //底部
	import {mapMutations,mapState} from 'vuex'
	export default {
		data() {
			return {
				International,
				clink: 3, //当前页
				ishowbg: false, //导航背景
				loading: false, //开启骨架屏
				introlist: [{
					vsrc: require('../../static/video/v4.mp4'),
					title: "OpenArt是全球领先的数字藏品电商平台",
					cont: "OpenArt定位于高端艺术品发行、流通。<br>OpenArt将重点纵深服务于大中华地区的玩家。<br>相比其它平台，OpenArt主打知名艺术品，其大部分产品具有稀缺性、珍藏性。<br>均与大众耳熟能详的知名IP、知名艺术家或公众人物联合发布，确保品质及收藏价值。",
					btxt: "阅读公告",
					link: "",
				}, {
					vsrc: require('../../static/video/v5.mp4'),
					title: "OpenArt将部署在多条公链（首发BSC）",
					cont: "具备安全、交易费用低、速度快等特性，支持ERC-721和ERC-1155标准的数字资产买卖，用户可以通过定价发行、盲盒发行、多形式拍卖等方式进行资产交易，同时配套多种流动性提升解决方案。",
					btxt: "阅读说明书",
					link: "",
				}, {
					vsrc: require('../../static/video/v6.mp4'),
					title: "OpenArt商业模式描述",
					cont: "1. 品牌类型：知名艺术IP品牌+Openart自营品牌；<br>2. 发行方式：限量定价发行、盲盒发行、多形式拍卖等等合理的价值发现方式；<br>3. 流动性方案：OpenArt将采取实体艺术品、实物周边等多种方案来增加的二次流动性；<br>4. 生态赋能：通过与众项目联动，扩大应用场景，为产品持续赋能，最终形成+实体IP为主的多领域协同发展生态。",
					btxt: "马上就来",
					link: "",
				}],
			}
		},

		components: {
			mHeader,
			mFooter
		},
		computed: mapState(["langType","isLogin","isfixed"]),
		created() {
			document.documentElement.scrollTop = 0;
		},
		mounted() {
			let that = this;
			window.addEventListener("scroll", this.handleScroll);
			setTimeout(() => (that.loading = false), 500);
		},
		methods: {
			//页面滚动
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //导航背景-灰色
				} else {
					that.ishowbg = false; //导航背景-白色
				}
			}
		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //销毁监听
		}
	}
</script>
