<template>
	<div id="login">
		<!-- 登录 -->
		<div class="ms-loginbox">
			<div class="ptit1">
				<img src="../../static/img/logo.png" class="img1">
				<h2>欢迎来到去中心化的艺术世界</h2>
			</div>
			<div class="box1">
				<el-tabs v-model="activeName" @tab-click="handleClick">
					<el-tab-pane label="账号登录" name="one">
						<div class="mbflex put">
							<i class="iconfont icon-zhanghao dic1"></i>
							<el-input v-model="uname" placeholder="请输入您的账号" class="ipu1"></el-input>
						</div>
						<div class="mbflex put">
							<i class="iconfont icon-mima dic1"></i>
							<el-input class="ipu1" placeholder="请输入登录密码" v-model="npsd" show-password></el-input>
						</div>
					</el-tab-pane>
					<el-tab-pane label="验证码登录" name="two">
						<div class="mbflex put">
							<i class="iconfont icon-shouji dic1"></i>
							<el-input maxlength="11" v-model="utel" placeholder="请输入手机号" class="ipu1"></el-input>
						</div>
						<div class="mbflex put">
							<i class="iconfont icon-yanzhengma dic1"></i>
							<el-input maxlength="4" v-model="uyzm" placeholder="请输入验证码" class="ipu1"></el-input>
							<button type="button" class="btn1">发送验证码</button>
						</div>
					</el-tab-pane>
				</el-tabs>
			</div>
			<div class="put box2">
				<el-checkbox v-model="icke">
					我已阅读并同意<label class="ar2" @click="showAgree">《用户协议》</label>
				</el-checkbox>
			</div>
			<button type="button" class="btntj" @click="submitForm(activeName)">登录</button>
			<div class="mbflex box2">
				<a href="" class="ar1">忘记密码？</a>
				<!-- <a href="" class="ar1">没有账号？<span class="ar2">立即注册</span></a> -->
			</div>
		</div>
		<!-- 登录 -->

		<!-- 用户协议-弹窗 -->
		<div class="ms-agreement trans5" :class="{'act':ishow1}">
			<div class="allbox-hd box1">
				<h2 class="pti1">用户协议</h2>
				<div class="pbox" v-for="(item,index) in 3" :key="index">
					欢迎您与OpenArt数字艺术电商平台经营者（详见定义条款）共同签署本《数字艺术电商平台服务协议》（下称“本协议”）并使用数字艺电商平台服务！<br>
					自本协议发布之日起，数字艺术电商平台各处所称“数字艺术电商平台服务协议”或“平台服务协议”均指本协议。<br>
					各服务条款前所列索引关键词仅为帮助您理解该条款表达的主旨之用，不影响或限制本协议条款的含义或解释。<br>
					为维护您自身权益，建议您仔细阅读各条款具体表述。<br>
					【审慎阅读】您在申请注册流程中点击同意本协议之前，应当认真阅读本协议。<br>
					请您务必审慎阅读、充分理解各条款内容，特别是免除或者限制责任的条款、法律适用和争议解决条款。<br>
					免除或者限制责任的条款将以粗体下划线标识，您应重点阅读。如您对协议有任何疑问，可向数字艺术电商平台客服咨询。<br>
					【签约】单击“登录”等足以表明您注册使用本数字艺术电商平台意愿的按钮，使用我们的服务和/或购买平台内展示的商品/服务，或按照页面提示填写信息、阅读并同意本协议且完成全部注册程序，即表示您已充分阅读、理解并接受本协议的全部内容及相应的法律责任，并与平台运营者达成一致，成为数字艺术电商平台“用户”。如果您不同意本协议或其中任何条款约定，您应立即停止注册程序且不得访问或使用服务或购买商品。(未成年人应当在其法定监护人的陪同下阅读)
					在您使用数字艺术电商平台之前，我们可能会要求您下载、使用支持公共区块链服务平台的软件、小程序或扩展程序（合称为“扩展程序”），并使用该扩展程序连接和解锁您的数字钱包。一旦您提交了出售或购买独特数字艺术品或/及其版权的订单，您的订单、藏品信息等就会在本网站显示，并与您的账户及手机号码上产生对应关系，由它代表您完成交易。您可以在需要的时候，提取对应的数字艺术品载体并传递到适用的扩展程序。
				</div>
				<button v-if="seconds > 0" type="button" class="btnclose trans5">
					<label class="sp1">({{seconds}}s)</label>
					我已阅读
				</button>
				<button v-else type="button" class="btnclose act trans5" @click="ishow1 = false">
					我已阅读
				</button>
			</div>
		</div>

		<!-- 用户协议-弹窗 -->
	</div>
</template>

<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'

	export default {
		name: 'login',
		data() {
			return {
				drawer: false, //是否显示弹窗
				activeName: 'one',
				uname: '', //账号
				npsd: '', //密码
				utel: '', //电话
				uyzm: '', //验证码
				icke: false, //是否同意用户协议
				seconds: 5, //当前秒数
				ishow1: false, //是否显示协议弹窗

			}
		},

		created() {

		},
		computed: mapState(["langType", "isLogin", "isfixed"]),
		methods: {
			...mapMutations(["changeLoginOld", "changePageOld"]),

			//切换选项卡
			handleClick(tab, event) {
				console.log(tab.name);
			},

			//登录
			submitForm(ctype) {
				let that = this;
				let uname = that.uname;
				let npsd = that.npsd;
				let utel = that.utel;
				let uyzm = that.uyzm;
				let icke = that.icke;
				if (ctype == 'one') {
					//账号登录
					that.$mpublic.isRequired(uname).then(() => {
						that.$mpublic.isRequired(npsd).then(() => {
							if (icke) {
								console.log(uname, npsd);
								that.drawer = false;
								that.changeLoginOld(true); //改变登录状态
								that.changePageOld(false); //页面是否禁止滚动
								setTimeout(function(){
									that.$router.push({name:'home'});
								},50);
							} else {
								that.$message.warning("请勾选《用户协议》");
							}
						}, err => {
							that.$message.warning("请输入您的账号~");
						})
					}, err => {
						that.$message.warning("请输入登录密码");
					})
				} else {
					//验证码登录
					that.$mpublic.isTel(utel).then(() => {
						that.$mpublic.isRequired(uyzm).then(() => {
						 if (icke) {
								console.log(utel, uyzm);
								that.drawer = false;
								that.changeLoginOld(true); //改变登录状态
								that.changePageOld(false); //页面是否禁止滚动
								setTimeout(function(){
									that.$router.push({name:'home'});
								},50);
							} else {
								that.$message.warning("请勾选《用户协议》");
							}
						}, err => {
							that.$message.warning("请输入验证码");
						})
					}, err => {
						that.$message.warning("请填写正确的手机号~");
						that.utel = '';
					})
				}
			},

			//显示弹窗
			showAgree() {
				let that = this;
				that.ishow1 = true;
				that.stimeAgree();
			},

			//计时器
			stimeAgree() {
				let that = this;
				let cnum = that.seconds;
				let timer1 = setInterval(function() {
					if (cnum > 0) {
						cnum -= 1;
						that.seconds = cnum;
					} else {
						clearInterval(timer1);
					}
				}, 1000);
			},

			//关闭回调
			setPageScro() {
				var that = this;
				that.changePageOld(false); //页面是否禁止滚动
			}

		}
	}
</script>

<style>
	.ms-loginbox {
		padding: 1rem 5%;
	}

	.ms-loginbox .ptit1 {
		text-align: center;
		font-size: 1.2rem;
		margin-bottom: 5rem;
	}

	.ms-loginbox .ptit1 .img1 {
		width: 80px;
		margin: 3rem auto;
	}

	.ms-loginbox .btntj {
		width: 100%;
		padding: 16px 0;
		color: #FFFFFF;
		border-radius: 5px;
		margin: 2rem auto;
		display: block;
		background: #000;
		font-size: 1.2rem;
	}

	.ms-loginbox .box1 {
		margin: 0 auto 4rem;
	}

	.ms-loginbox .box1 .put {
		padding: 2.4rem 0 1.4rem;
		border-bottom: 1px solid #d9d9d9;
		white-space: nowrap;
	}

	.ms-loginbox .box1 .dic1 {
		font-size: 1.6rem;
	}

	.ms-loginbox .box1 .ipu1 {
		flex-grow: 4;
		font-size: 1rem;
		padding: 0 1rem;
	}

	.ms-loginbox .box1 .el-input__inner {
		border: none;
		padding: 0;
	}

	.ms-loginbox .box1 .btn1 {
		padding: 10px 16px;
		border: 1px solid #BFBFBF;
		border-radius: 5px;
		cursor: pointer;
	}

	.ms-loginbox .box2 .ar1 {
		color: #5C5C5C;
	}

	.ms-loginbox .box2 .ar2 {
		color: #3aaaf9;
		cursor: pointer;
	}

	.ms-agreement {
		width: 100%;
		height: 100%;
		overflow: hidden;
		overflow-y: auto;
		background: #FFFFFF;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 2020;
		pointer-events: none;
		transform: scale(1.2);
		opacity: 0;
	}

	.ms-agreement .box1 {
		padding: 3rem 5%;
	}

	.ms-agreement .pti1 {
		text-align: center;
		margin-bottom: 2rem;
	}

	.ms-agreement .pbox {
		font-size: 1.14rem;
		line-height: 2.4rem;
		color: #333333;
	}

	.ms-agreement .btnclose {
		width: 240px;
		line-height: 3.8rem;
		margin: 3rem auto 1rem;
		background: #e3e3e3;
		display: block;
		border-radius: 50px;
		font-size: 1.2rem;
		color: #919191;
		cursor: pointer;
	}

	.ms-agreement .btnclose .sp1 {
		margin-right: 6px;
	}

	.ms-agreement .btnclose.act {
		background: #000000;
		color: #FFFFFF;
	}

	.ms-agreement.act {
		pointer-events: auto;
		transform: scale(1);
		opacity: 1;
	}
</style>
