<template>
	<div id="wallet">
		<!-- 绑定钱包 -->
		<el-drawer title="绑定钱包" :size="drawerSize" custom-class="mdrawer" :visible.sync="drawer" @close="setPageScro">
			<div class="walletbox1">
				<h2 class="ptit1">请选择您要绑定的钱包类型</h2>
				<p class="pti2">1、已有钱包，立即绑定</p>
				<div class="mbflex box1">
					<input type="text" v-model="waddr" placeholder="请输入或粘贴您的钱包地址" class="ipu1" />
					<button type="button" class="btntj dwr" @click="submitForm1(waddr)">立即绑定</button>
				</div>
				<p class="pti2">2、没有钱包，马上创建</p>
				<button type="button" class="btntj" @click="submitForm2()">我要创建</button>
			</div>
		</el-drawer>
		<!-- 绑定钱包 -->
	</div>
</template>

<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'
	
	export default {
		name: 'wallet',
		data() {
			return {
				drawerSize:'400px', //抽屉宽度
				drawer: false, //打开绑定钱包弹窗
				waddr: '', //绑定的钱包地址
			}
		},
		computed: mapState(["langType","isLogin","isfixed"]),
		methods: {
			...mapMutations(["changeLoginOld","changePageOld"]),
			
			//显示弹窗
			showAlert() {
				let that = this;
				that.drawer = true;
				that.changePageOld(true); //页面是否禁止滚动
			},
			
			//绑定钱包
			submitForm1(waddr) {
				var that = this;
				that.$mpublic.isRequired(waddr).then(() => {
					console.log(waddr);
					that.drawer = false;
					that.uwallet = waddr;
					that.$message.success("绑定成功");
					that.changePageOld(false); //页面是否禁止滚动
					that.$emit('setWaddr',waddr);
				}, err => {
					that.$message.warning("请输入或粘贴您的钱包地址");
				})
			},
			
			//创建钱包
			submitForm2() {
				var that = this;
				const loading = this.$loading({
					lock: true,
					text: '创建中...',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.6)'
				});
				setTimeout(() => {
					loading.close();
					let waddr = "0xEBe0HOWERHjda802C83f5"
					that.drawer = false;
					that.uwallet = waddr;
					that.$message.success("创建成功");
					that.$emit('setWaddr',waddr);
					that.changePageOld(false); //页面是否禁止滚动
				}, 2000);
			},
			
			//关闭回调
			setPageScro(){
				var that = this;
				that.changePageOld(false); //页面是否禁止滚动
			}
			
		}
	}
</script>

<style>
	
	.walletbox1 {
		padding: 1rem 5%;
	}
	
	.walletbox1 .ptit1 {
		text-align: center;
		font-size: 2rem;
		margin-bottom: 5rem;
	}
	
	.walletbox1 .pti2 {
		font-size: 1.2rem;
		margin: 5rem auto 2rem;
	}
	
	.walletbox1 .btntj {
		width: 100%;
		height: 4rem;
		line-height: 4rem;
		color: #FFFFFF;
		border-radius: 5px;
		margin: 1rem auto;
		display: block;
		background: #000;
		font-size: 1.2rem;
		cursor: pointer;
	}
	
	.walletbox1 .box1 {
		height: 4rem;
		overflow: hidden;
		border: 1px solid #000000;
		border-radius: 5px;
	}
	
	.walletbox1 .box1 .ipu1 {
		height: 4rem;
		line-height: 4rem;
		padding: 0 12px;
		flex-grow: 4;
	}
	
	.walletbox1 .box1 .dwr {
		flex: 0 0 8rem;
	}
	
	.el-loading-spinner i {
		font-size: 3rem;
		color: #FFFFFF;
	}
	
	.el-loading-spinner .el-loading-text {
		color: #FFFFFF;
		margin-top: 8px;
	}
	
</style>
