					img: require('../../static/video/v1.mp4'),
					type: 1, //1=视频，2=图片
					user_head: "https://img0.baidu.com/it/u=680725,648018937&fm=26&fmt=auto",
					user_name: "Creator",
					user_name1: "Boss",
					user_name2: "@peajey",
					title: "LIST PRICE",
					bgcol: "#484848",
					price1: 0.1,
					price2: 290.59,
					iend: false, //是否已售罄
				}, {
					img: require('../../static/video/v2.mp4'),
					type: 1, //1=视频，2=图片
					user_head: "https://img0.baidu.com/it/u=1517838256,1457572322&fm=26&fmt=auto",
					user_name: "Creator",
					user_name1: "Serenity",
					user_name2: "@peajey",
					title: "LIST PRICE",
					bgcol: "#281372",
					price1: 0.2,
					price2: 581.17,
					iend: true, //是否已售罄
				}, {
					img: require('../../static/img/pic_01.jpg'),
					type: 2, //1=视频，2=图片
					user_head: "https://file.cryptoart.ai/avatar/2021/3/17/SMOOCHIE-PROFILE-PIC-CRYPTOART_1618671043194.gif?x-oss-process=image/resize,m_lfit,w_128",
					user_name: "Creator",
					user_name1: "星辰",
					user_name2: "@peajey",
					title: "LIST PRICE",
					bgcol: "#07575f",
					price1: 0.1,
					price2: 290.59,
					iend: false, //是否已售罄
				}, {
					img: require('../../static/img/pic_03.jpg'),
					type: 2, //1=视频，2=图片
					user_head: "https://file.cryptoart.ai/avatar/2021/6/20/20210325_124434_1626788244768.gif?x-oss-process=image/resize,m_lfit,w_128",
					user_name: "Creator",
					user_name1: "大无畏",
					user_name2: "@yangxin",
					title: "LIST PRICE",
					bgcol: "#452b3e",
					price1: 0.1,
					price2: 290.59,
					iend: true, //是否已售罄
				}, {
					img: require('../../static/img/pic_06.gif'),
					type: 2, //1=视频，2=图片
					user_head: "https://img0.baidu.com/it/u=1249136520,1485656026&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=694",
					user_name: "Creator",
					user_name1: "Boss",
					user_name2: "@peajey",
					title: "LIST PRICE",
