					img: require('../../static/img/pic_05.jpg'),
					type: 2, //1=视频，2=图片
					user_head: "https://img2.baidu.com/it/u=3109263405,2074103&fm=253&fmt=auto&app=138&f=JPEG?w=600&h=400",
