		components: {
			mHeader,
			mFooter,
			Goodslist
		},
		computed: mapState(["langType", "isLogin", "isfixed", "mcastlist"]),
		created() {
			document.documentElement.scrollTop = 0;
		},
		mounted() {
			let that = this;
			setTimeout(() => (that.loading = false), 300);
			window.addEventListener("scroll", this.handleScroll);
		},
		methods: {

			//页面滚动
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //导航背景-灰色
				} else {
					that.ishowbg = false; //导航背景-白色
				}
			},

			//切换1
			checkTab1(idx) {
				let that = this;
				that.cidx1 = idx;
				that.cidx2 = 0;
				that.cidx3 = 0;
			},

			//切换2
			checkTab2(idx) {
				let that = this;
				that.cidx2 = idx;
			},

			//切换3
			checkTab3(idx) {
				let that = this;
				that.cidx3 = idx;
			},

			//隐藏筛选
			hideSer() {
				let that = this;
				that.ihide = !that.ihide;
			}

		},

		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //销毁监听
		}
	}
</script>

<style>

	.mallbox .mserbox {
