		position: fixed;
		top: 100px;
		left: 0;
		z-index: 99;
		padding: 0 24px;
		overflow: hidden;
		overflow-y: auto;
	}

	.mallbox .mserbox .btns1 {
		float: right;
		font-size: 2rem;
		position: relative;
		right: 0;
	}

	.mallbox .mserbox .btns2 {
		right: -18px;
	}

	.mallbox .mserbox .pt1 {
		margin: 1rem auto;
		position: relative;
	}

