<template>
	<div id="user">
		<mHeader :clink='4' :ishow="ishowbg"></mHeader>
		<div class="serbox1">
			<div v-for="(ritem,ridx) in tablist" :key="ridx">
				<router-link class="mbflex dli" :to="ritem.link">
					<label class="sp1">{{ritem.name}}</label>
					<i class="iconfont icon-xiangyoujiantou dic1"></i>
				</router-link>
			</div>
		</div>

		<button type="button" class="rexit" @click="setExit">
			<i class="iconfont icon-logout dic1"></i>退出
		</button>

	</div>
</template>

<script>
	import International from '../lib/international.js' //语言包
	import mHeader from '@/components/mheader' //顶部

	import {
		mapMutations,
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				International,
				ishowbg: false, //导航背景
				tablist: [{
					id: 11,
					name: "系统消息",
					link: "/userNews/系统消息"
				}, {
					id: 14,
					name: "互动消息",
					link: "/"
				}, {
					id: 8,
					name: "个人资料",
					link: "/userInfo"
				}, {
					id: 9,
					name: "实名认证",
					link: "/userIndentify"
				}, {
					id: 10,
					name: "隐私设置",
					link: "/userSet"
				}], //导航
			}
		},

		components: {
			mHeader
		},
		created() {
			document.documentElement.scrollTop = 0;
		},
		mounted() {
			let that = this;
			window.addEventListener("scroll", this.handleScroll);
		},
		computed: mapState(["langType", "isLogin", "isfixed"]),
		methods: {
			...mapMutations(["changeLoginOld", "changePageOld"]),

			//页面滚动
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //导航背景-灰色
				} else {
					that.ishowbg = false; //导航背景-白色
				}
			},

			//退出登录
			setExit() {
				let that = this;
				that.$confirm('退出登录后将不能继续使用个人中心的功能', '确定退出?', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					that.changeLoginOld(false); //改变登录状态
					that.$router.push({
						path: '/'
					}); //跳回首页
				}).catch(() => {});
			}


		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //销毁监听
		}
	}
</script>

<style>
	.serbox1{
		padding: 0 2%;
	}
	.serbox1 .dli{
		font-size: 1.24rem;
		color: #000;
		padding: 1.4rem 2%;
		border-bottom: 1px solid #f2f2f2;
	}
	
	.rexit{
		width: 100%;
		text-align: center;
		padding: 2rem;
		color: #999;
		font-size: 1.2rem;
	}
	.rexit .dic1{
		font-size: 1.2rem;
		margin: 0 4px;
	}
	
</style>
