<template>
	<div id="mheader">
		<div class="mheader" :class="{'bgray':ishow}">
			<div class="allbox mbflex box1">
				<i class="el-icon-s-fold dmenu1 hidden-md-and-up" @click="showBox1"></i>
				<router-link to="/">
					<img src="../../static/img/logo.png" class="dlo">
				</router-link>
				<div class="mserch hidden-md-and-down">
					<i class="iconfont icon-sousuo dic1"></i>
					<input type="text" class="ipu1" v-model="skey" placeholder="艺术家、ID" />
				</div>
				<div class="mbflex navbox">
					<div class="dl hidden-sm-and-down" :class="{'act':clink==item.id}" v-for="(item,midx) in linkListPC"
						:key="midx">
						<div v-if="item.child.length > 0">
							<el-popover placement="bottom-end" trigger="hover">
								<div class="ndownlis">
									<div v-for="ritem in item.child" :key="ritem.name">
										<router-link class="rdl" :to="ritem.link">{{ritem.name}}</router-link>
									</div>
								</div>
								<div class="bg1 p1" slot="reference">
									<p>{{item.name}}<i class="iconfont icon-below-s dic1"></i></p>
								</div>
							</el-popover>
						</div>
						<router-link v-else :to="item.link">
							<p class="bg1 p2">{{item.name}}</p>
						</router-link>
					</div>

					<router-link class="hidden-sm-and-down" v-if="isLogin" to="/userHome" slot="reference">
						<div class="mbflex dl">
							<el-image :src="user_head" fit="cover" class="musr_head"></el-image>
						</div>
					</router-link>
					<!-- <el-popover placement="bottom-end" trigger="hover">
						<div class="ndownlis">
							<router-link class="rdl" to="/userHome">个人主页</router-link>
							<button type="button" class="rdl" @click="setExit">退出</button>
						</div>
						<div slot="reference">
							<router-link class="hidden-sm-and-down" v-if="isLogin" to="/userHome" slot="reference">
								<div class="mbflex dl">
									<el-image :src="user_head" fit="cover" class="musr_head"></el-image>
								</div>
							</router-link>
						</div>
					</el-popover> -->
				</div>

				<!-- <button type="button" v-if="!isLogin" class="btn1 hidden-sm-and-down trans3" @click="contactWallt">
					<p class="p1 trans3">连接</p>
					<p class="p2 trans3">连接</p>
				</button> -->
			</div>
		</div>

		<!-- 手机导航 -->
		<transition name="fade">
			<div class="winflbg" @click="hideBox1()" v-show="ishow1"></div>
		</transition>
		<div class="menubox trans5" :class="{'act' : ishow1}">
			<div class="mbflex pti1">
				<router-link to="/">
					<img src="../../static/img/logo.png" class="dlo">
				</router-link>
				<i class="el-icon-s-unfold dic1" @click="hideBox1()"></i>
			</div>
			<router-link :to="item.link" v-for="(item,index) in linklistWap" :key="index">
				<div class="dl">{{item.name}}</div>
			</router-link>
			<router-link v-if="isLogin" to="/userHome">
				<div class="dl">
					<el-image :src="user_head" fit="cover" class="musr_head"></el-image>
				</div>
			</router-link>
			<!-- <router-link v-else to="/login">
				<button class="mbtn1">登录</button> 
			</router-link> -->
			<button type="button" v-else class="mbtn1" @click="contactWallt">连接</button>
		</div>
		<!-- 手机导航 -->

		<div class="msolt-top"></div>

		<!-- 登录弹窗 -->
		<Login ref="mlogin"></Login>
		<!-- 登录弹窗 -->

	</div>
</template>

<script>
	import International from '../lib/international.js' //语言包
	import Login from '@/components/login' //登录
	import {
		getAccount
	} from '@/util/myconnect'
	import {
		mapMutations,
		mapState
	} from 'vuex'

	const Web3 = require('../util/getWeb3.js')
	export default {
		name: 'mheader',
		data() {
			return {
				International,
				skey: '', //搜索关键字
				ishow1: false, //是否显示左侧导航
				visible1: false,
				cidx: 0, //当前语言
				langlist: [{
					type: 'EN',
					name: 'English'
				}, {
					type: 'ZH_CN',
					name: '中文（简）'
				}], //语言
				user_head: require('../../static/img/ban_03.jpg'),
				linkListPC: [{
						name: '馆',
						link: '/primaryMarket',
						id: 1,
						child: []
					},
					{
						name: '探索',
						id: 2,
						link: '/artists',
						child: []
					}, {
						name: '铸造',
						id: 5,
						link: '/userPublish',
						child: []
					},
					{
						name: 'OpenArt',
						id: 3,
						link: '/cart',
						child: []
					}
				], //导航链接pc
				linklistWap: [{
						name: '馆',
						id: 1,
						link: '/primaryMarket',
					}, {
						name: '探索',
						id: 2,
						link: '/artists',
					},
					{
						name: 'OpenArt',
						id: 3,
						link: '/cart',
					}
				], //导航链接手机
			}
		},
		props: {
			clink: Number, //当前链接id
			ishow: Boolean, //是否显示灰色背景
		},
		mounted() {
			let that = this;
			that.contactWallt();
		},
		components: {
			Login
		},
		computed: mapState(["langType", "isLogin", "isfixed"]),
		methods: {
			...mapMutations(["changeLanguageOld", "changeLoginOld", "changePageOld"]),

			// //连接钱包
			contactWallt() {
				let that = this;
				Web3.default.getWeb3().then(res => {
					if (res) {
						that.changeLoginOld(true); //改变登录状态
					} else {
						that.changeLoginOld(false); //改变登录状态
					}
				}, err => {
					that.changeLoginOld(false); //改变登录状态
				})
			},

			//切换语言
			changeLanguage(clang, cidx) {
				let that = this;
				that.cidx = cidx;
				that.visible1 = false;
				that.changeLanguageOld(clang); //改变状态
			},

			//显示登录弹窗
			showAlert() {
				let that = this;
				that.$refs.mlogin.showAlert(true);
			},

			//显示弹窗-左侧
			showBox1() {
				let that = this;
				that.ishow1 = true;
			},

			//关闭弹窗-左侧
			hideBox1() {
				let that = this;
				that.ishow1 = false;
			},

			//退出登录
			setExit() {
				let that = this;
				that.$confirm('退出登录后将不能继续使用个人中心的功能', '确定退出?', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					that.changeLoginOld(false); //改变登录状态
					let cname = that.$route.name;
					if (cname == 'home') {
						console.log(cname);
					} else {
						that.$router.push({
							path: '/'
						}); //跳回首页
					}
				}).catch(() => {});
			},

		}
	}
</script>

<style>
	.msolt-top {
		height: 70px;
	}

	.mheader {
		width: 100%;
		position: fixed;
		top: 0;
		left: 0;
		height: 70px;
		z-index: 1000;
		background: #FFFFFF;
	}

	.mheader.bgray {
		background: hsla(0, 0%, 94.1%, .5);
		-webkit-backdrop-filter: blur(20px);
		backdrop-filter: blur(20px);
		transform: translateZ(0);
	}

	.mheader .box1 {
		height: 100%;
	}

	.mheader .dlo {
		height: 2.6rem;
	}

	.mheader .mserch {
		background: #fff;
		border-radius: 40px;
		border: 1px solid #dcdfe6;
		color: #606266;
		padding: 8px 14px;
		white-space: nowrap;
		margin-left: 1rem;
	}

	.mheader .mserch .dic1 {
		margin-right: 8px;
	}

	.mheader .mserch .ipu1 {
		border-left: 1px solid #a9a9a9;
		padding-left: 12px;
	}

	.navbox {
		margin: 0 4rem;
		flex-grow: 3;
		white-space: nowrap;
		justify-content: flex-end;
	}

	.navbox .dl {
		position: relative;
		margin-left: 4rem;
		cursor: pointer;
	}

	.navbox .dl .bg1 {
		height: 40px;
		line-height: 40px;
		color: #444444;
		font-weight: bold;
		position: relative;
		transition: all .2s;
		top: 0;
	}

	.navbox .dl .p2::after {
		content: "";
		width: 0;
		height: 2px;
		background: #000000;
		margin: 0 auto;
		display: block;
		transition: all .3s;
	}

	.navbox .dl .dic1 {
		margin-left: 8px;
		font-size: .9rem;
	}

	.ndownlis .rdl {
		width: 100%;
		color: #000000;
		white-space: nowrap;
		display: block;
		padding: 8px 0;
		cursor: pointer;
		text-align: center;
	}

	.ndownlis .rdl.bgx {
		border-top: 1px solid #F2F2F2;
		margin-top: 8px;
		padding-top: 16px;
		opacity: .8;
	}

	.navbox .dl:hover .p2 {
		transform: scale(1.1);
		color: #000000;
		top: -5px;
	}

	.ndownlis .rdl:hover,
	.navbox .dl .rdl:hover {
		font-weight: bold;
	}

	.navbox .dl.act .p2 {
		transform: scale(1.1);
		color: #000000;
		top: -5px;
	}

	.navbox .dl.act .p2::after,
	.navbox .dl:hover .p2::after {
		width: 80%;
	}

	.mheader .btn1 {
		width: 100px;
		height: 36px;
		line-height: 36px;
		border-radius: 40px;
		white-space: nowrap;
		text-align: center;
		position: relative;
		letter-spacing: 1px;
		cursor: pointer;
		font-weight: bold;
		background: #000000;
		color: #FFFFFF;
	}

	.mheader .btn1 .p2 {
		width: 100%;
		height: 36px;
		line-height: 36px;
		color: #000;
		background: #FFFFFF;
		border: 1px solid;
		border-radius: 40px;
		overflow: hidden;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 2;
		transform: scaleY(0);
	}

	.mheader .btn1:hover .p1 {
		transform: rotate(180deg);
	}

	.mheader .btn1:hover {
		transform: scale(.9);
	}

	.mheader .btn1:hover .p2 {
		transform: scale(1.1);
	}

	.mheader .dlink {
		position: relative;
		margin-left: 1rem;
		cursor: pointer;
	}

	.mheader .dlink .bg1 {
		line-height: 30px;
		background: #f2f2f2;
		border-radius: 4px;
		padding: 0 12px;
		white-space: nowrap;
	}

	.downlis {
		overflow: hidden;
	}

	.downlis .rdl {
		color: #000000;
		white-space: nowrap;
		display: block;
		line-height: 38px;
	}

	.downlis .rdl .dic1 {
		margin-right: 4px;
	}

	.downlis .rdl:hover {
		font-weight: bold;
	}


	/* 手机导航 */
	.mheader .dmenu1 {
		font-size: 2rem;
		margin-right: 1rem;
	}

	.menubox {
		width: 70%;
		height: 100%;
		overflow: hidden;
		background: #FFFFFF;
		position: fixed;
		left: -110%;
		top: 0;
		z-index: 1012;
	}

	.menubox.act {
		left: 0;
	}

	.menubox .pti1 {
		padding: 1rem 4%;
		border-bottom: 1px solid #ebebeb;
		margin-bottom: 1rem;
	}

	.menubox .pti1 .dlo {
		width: 2.8rem;
		height: 2.8rem;
	}

	.menubox .pti1 .dic1 {
		font-size: 2rem;
		padding: 8px;
	}

	.menubox .dl {
		font-size: 1.2rem;
		padding: 22px 8%;
		color: #000000;
	}

	.menubox .mbtn1 {
		margin: 2rem 8%;
		padding: .8rem 2.6rem;
		background: #000000;
		font-size: 1.2rem;
		color: #FFFFFF;
		border-radius: 4px;
	}

	/* 手机导航 */

	.musr_head {
		width: 2.8rem;
		height: 2.8rem;
		border-radius: 50%;
		box-shadow: 0 0 8px 4px #dedede;
	}

	@media screen and (max-width: 768px) {

		.msolt-top,
		.mheader {
			height: 4rem;
		}
	}
</style>
