<template>
	<div class="uindentify">
		<h2 class="putit1">实名认证</h2>
		<h4 class="mtip"><i class="el-icon-message-solid dic1"></i>请确保实名信息真实一致，OpenArt安全保障中，身份信息仅用于实名认证</h4>

		<div v-if="ishow">
			<!-- 个人中心-实名认证 -->
			<h3 class="ipti1">1、选择认证方式</h3>
			<div class="mbflex box1">
				<button type="button" class="dl trans3" :class="{'act':item.iact,'bg1': ctype==index}"
					v-for="(item,index) in typelist" @click="checkForm(index)" :key="index" :disabled="item.iact">
					<i class="iconfont dic1" :class="item.dic"></i>
					<p class="p1">{{item.title}}</p>
					<div class="stag" v-if="item.iact"><i class="iconfont icon-renzheng dic2"></i>已认证</div>
					<div class="stag trans3" v-else><i class="iconfont icon-renzheng dic2"></i>未认证</div>
				</button>
			</div>
			<div class="mbflex btnbox">
				<button type="button" class="dl" @click="showBox(ctype)">下一步</button>
			</div>
		</div>

		<!-- 个人认证 -->
		<div class="box2" v-if="ishow1">
			<h3 class="ipti1">2、完善资料</h3>
			<div class="uformbox">
				<div class="put">
					<p class="sp1 cred">真实姓名</p>
					<el-input v-model="uname" placeholder="请输入真实姓名" class="ipu1"></el-input>
				</div>
				<div class="put">
					<p class="sp1 cred">身份证号</p>
					<el-input v-model="uid" placeholder="请输入真实有效的身份证号码" class="ipu1"></el-input>
				</div>
				<div class="put">
					<p class="sp1 cred">上传身份证照片</p>
					<div class="mbflex dimgs">
						<div class="dl">
							<el-image :src="uimgz" fit="cover" class="img1">
								<div slot="error" class="mbflex img2">
									<div class="pwz">
										<i class="iconfont icon-shenfenzhengzhengmian dic1"></i>
										<p>正面人像面</p>
									</div>
								</div>
							</el-image>
						</div>
						<div class="dl">
							<el-image :src="uimgf" fit="cover" class="img1">
								<div slot="error" class="mbflex img2">
									<div class="pwz">
										<i class="iconfont icon-shenfenzhengfanmian dic1"></i>
										<p>反面国徽面</p>
									</div>
								</div>
							</el-image>
						</div>
					</div>
				</div>
			</div>
			<div class="mbflex btnbox">
				<button type="button" class="dl bg1" @click="goBack1()"><i class="el-icon-back dic1"></i> 上一步</button>
				<button type="button" class="dl" @click="submit1(uname,uid,uimgz,uimgf)">提交</button>
			</div>
		</div>

		<!-- 企业认证 -->
		<div class="box2" v-if="ishow2">
			<h3 class="ipti1">2、完善资料</h3>
			<div class="uformbox">

			</div>
			<div class="mbflex btnbox">
				<button type="button" class="dl bg1" @click="goBack1()">上一步</button>
				<button type="button" class="dl" @click="submit2()">提交</button>
			</div>
		</div>

		<!-- 艺术家认证 -->
		<div class="box2" v-if="ishow3">
			<h3 class="ipti1">2、完善资料</h3>
			<div class="uformbox">

			</div>
			<div class="mbflex btnbox">
				<button type="button" class="dl bg1" @click="goBack1()">上一步</button>
				<button type="button" class="dl" @click="submit3()">提交</button>
			</div>
		</div>

	</div>
</template>

<script>
	import International from '../lib/international.js' //语言包
	import {
		mapMutations,
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				International,
				ctab_name: "", //当前标题
				ctype: 0,
				typelist: [{
						title: '个人认证',
						dic: "icon-gerenrenzheng",
						iact: false, //是否已认证
					},
					{
						title: '企业认证',
						dic: "icon-qiyerenzheng",
						iact: true, //是否已认证
					},
					{
						title: '艺术家认证',
						dic: "icon-gerenrenzheng1",
						iact: false, //是否已认证
					}
				], //认证方式
				ishow: true, //第一步
				ishow1: false, //个人认证
				ishow2: false, //企业认证
				ishow3: false, //艺术家认证
				uname: "", //姓名
				uid: "", //身份证号
				uimgz: "", //身份证-正面
				uimgf: "", //身份证-反面

			}
		},
		created() {
			document.documentElement.scrollTop = 0;
		},
		
		computed: mapState(["langType", "isLogin", "isfixed"]),
		methods: {
			//选择表单
			checkForm(idx) {
				let that = this;
				that.ctype = idx;
			},

			//显示表单
			showBox(ctype) {
				let that = this;
				if (ctype == 0) {
					that.ishow1 = true;
				} else if (ctype == 1) {
					that.ishow2 = true;
				} else {
					that.ishow3 = true;
				}
				that.ishow = false;
			},

			//回到第一步
			goBack1() {
				let that = this;
				that.ishow = true;
				that.ishow1 = false;
				that.ishow2 = false;
				that.ishow3 = false;
			},

			//提交个人认证
			submit1(uname, uid, uimgz, uimgf) {
				let that = this;

			}

		},

	}
</script>

<style>
	.uindentify .mtip {
		background: #E3E3E3;
		padding: 1rem;
	}

	.uindentify .mtip .dic1 {
		margin-right: 8px;
	}

	.uindentify .ipti1 {
		padding: 3rem 0;
	}

	.uindentify .btnbox {
		justify-content: center;
		margin: 4rem auto;
	}

	.uindentify .btnbox .dl {
		background: #000;
		color: #FFFFFF;
		border-radius: 40px;
		padding: 16px 0;
		margin: 0 2rem;
		font-size: 1.2rem;
		text-align: center;
		width: 200px;
	}

	.uindentify .btnbox .dl.bg1 {
		background: #FFFFFF;
		color: #000000;
		border: 1px solid;
	}

	.uindentify .btnbox .dic1 {
		margin-right: 12px;
	}

	.uindentify .btnbox1 {
		justify-content: flex-start;
	}

	.uindentify .box1 {
		margin: 3rem auto 6rem;
	}

	.uindentify .box1 .dl {
		flex: 0 1 26%;
		color: #808080;
		border: 1px solid #E3E3E3;
		border-radius: 8px;
		padding: 2rem 0;
		text-align: center;
		position: relative;
	}

	.uindentify .box1 .stag {
		position: absolute;
		background: #FFFFFF;
		color: #858585;
		border-radius: 5px;
		padding: 8px 14px;
		top: -14px;
		right: -14px;
		z-index: 2;
	}

	.uindentify .box1 .dic1 {
		font-size: 3rem;
	}

	.uindentify .box1 .dic2 {
		margin-right: 6px;
	}

	.uindentify .box1 .p1 {
		font-size: 1.2rem;
		margin-top: 2rem;
	}

	.uindentify .box1 .dl.act {
		background: #f9f9f9;
	}

	.uindentify .box1 .dl.act .stag {
		background: #e7e7e7;
	}

	.uindentify .box1 .dl.bg1 {
		border-color: #000;
		color: #000;
	}

	.uindentify .box1 .dl.bg1 .stag {
		color: #000;
	}

	.uindentify .box2 {
		width: 700px;
	}
</style>
