import Vue from 'vue'
import Router from 'vue-router'
// import '@/assets/iconfont.css'
import '@/assets/style.css'
import '@/assets/index.css'
import '@/assets/rcontent.css'

Vue.use(Router)

import login from '@/views/login' //登录
import home from '@/views/home' //首页
import primaryMarket from '@/views/primaryMarket' //馆
import goodsDetail from '@/views/goods_detail' //商品详情
import goodsDetailCast from '@/views/goods_detail_cast' //商品详情--已铸造
import goodsDetailMH from '@/views/goods_detail_mh' //盲盒详情
import artists from '@/views/artists' //探索
import userDetail from '@/views/user_detail' //艺术家详情
import cart from '@/views/cart' //关于
import user from '@/views/user' //个人中心 
import userHome from '@/views/user_home' //个人中心--主页
import userProduct from '@/views/user_product' //个人中心--我的创建
import userPublish from '@/views/user_publish' //个人中心--新建作品
import userOrderMall from '@/views/user_order_mall' //个人中心--收到订单
import userOrderJudge from '@/views/user_order_judge' //个人中心--收到报价
import userOrder from '@/views/user_order' //个人中心--我的拥有
import userBlindBox from '@/views/user_blindBox' //个人中心--我的盲盒
import userNews from '@/views/user_news' //个人中心--系统消息
import userInfo from '@/views/user_info' //个人中心--个人资料
import userIndentify from '@/views/user_indentify' //个人中心--实名认证
import userSet from '@/views/user_set' //个人中心--隐私设置
import userSetMS from '@/views/user_set_ms' //个人中心--手机设置

export default new Router({
	mode: 'hash', //路由的 history 模式
	routes: [
		{path: '/',name: 'home',component: home},
		{path: '/login',name: 'login',component: login},
		{path: '/primaryMarket',name: 'primaryMarket',component: primaryMarket},
		{path: '/artists',name: 'artists',component: artists},
		{path: '/cart',name: 'cart',component: cart},
		{path: '/goodsDetail/:id',name: 'goodsDetail',component: goodsDetail},
		{path: '/goodsDetailMH/:id',name: 'goodsDetailMH',component: goodsDetailMH},
		{path: '/goodsDetailCast/:id',name: 'goodsDetailCast',component: goodsDetailCast},
		{path: '/userDetail/:id',name: 'userDetail',component: userDetail},
		{path: '/userPublish',name: 'userPublish',component: userPublish},
		{path: '/userSetMS',name: 'userSetMS',component: userSetMS},
		{
			path: '/user',
			name: 'user',
			component: user,
			children: [
				{path: '/userHome',name: 'userHome',component: userHome},
				{path: '/userProduct',name: 'userProduct',component: userProduct},
				{path: '/userOrderMall',name: 'userOrderMall',component: userOrderMall},
				{path: '/userOrderJudge',name: 'userOrderJudge',component: userOrderJudge},
				{path: '/userOrder',name: 'userOrder',component: userOrder},
				{path: '/userBlindBox',name: 'userBlindBox',component: userBlindBox},
				{path: '/userNews/:id',name: 'userNews',component: userNews},
				{path: '/userInfo',name: 'userInfo',component: userInfo},
				{path: '/userIndentify',name: 'userIndentify',component: userIndentify},
				{path: '/userSet',name: 'userSet',component: userSet},
			]
		},
	]
})
